.button-label {
    font-size: 15px;
    font-family: Impact, Charcoal, sans-serif;
    text-align: center;
    color: black;
    text-shadow: -1px 1 black, 1 3px black, 3px 1 black, 1 1px black;
    border: none;
}

.expansion-panel {
    width: 200px;
    height: 300px;
}

.expansion-container {
    width: 200px;
    box-shadow: inset 0px 0px 20px 8px rgb(235, 118, 8);
    border: 2px solid rgb(3, 0, 0);
}

.days-container {
    width: 200px;
    height: 25px;
    margin: 10px 5px 0 5px;
    display: inline-flex;
    flex-flow: row;
    padding: 10px 10px;
}

.daybox:focus {
    color: rgb(228, 133, 8);
}

.daybox {
    box-shadow: inset 0px 0px 20px 8px rgb(235, 118, 8);
    width: 28px;
}

.grid-container {
    height: 50px;
    padding: 10px 10px;
}

.time-container {
    width: 80px;
    height: 30px;
    display: flex;
    align-content: left;
    justify-content: flex-end;
}

.slider-container {
    width: 200px;
    display: flex;
    justify-content: center;
}

.slider {
    width: 100px;
    padding: 22px 0px;
    color: black;
}