
.city-selector-container {
    width: 100%;
    display: flex;
    box-shadow: inset 0px 0px 20px 8px rgb(235, 118, 8);
    border: 2px solid rgb(3, 0, 0);
}

.city-selector-label {
    font-size: 13px;
    font-family: Impact, Charcoal, sans-serif;
    margin-left: 30px;
    color: black;
    text-shadow: -1px 1 black, 1 3px black, 3px 1 black, 1 1px black;
}

.select-btn {
    width: 100%;
    border: 2px solid rgb(3, 0, 0);
}

/* cities */
.city-list-container { 
    box-shadow: inset 0px 0px 20px 8px rgb(235, 118, 8);
    border: 2px solid rgb(3, 0, 0);
}

.city-name-container {
    box-shadow: 4px 5px;
    border: 2px solid rgb(3, 0, 0);
}
.city-list-item {
    margin-left: 50px;
    font-size: 15px;
    font-family: Impact, Charcoal, sans-serif;
    text-align: right;
    color: black;
    text-shadow: -1px 1 black, 1 3px black, 3px 1 black, 1 1px black;
}

