.switch-container {
    width: 100%;
    height: 50px;
    display: flex;
    box-shadow: inset 0px 0px 20px 8px rgb(235, 118, 8);
    border: 2px solid rgb(3, 0, 0);
    justify-content: flex-end;
}

.content-container {
    display: flex;
    flex-direction: row-reverse;
}

.switch-text {
    font-size: 13px;
    font-family: Impact, Charcoal, sans-serif;
    margin-left: 30px;
    color: black;
    text-shadow: -1px 1 black, 1 3px black, 3px 1 black, 1 1px black;
}
