.history-menu {
    position: "absolute";
    right: "5%";
    top: "2%";
    border-radius: "3px";
    height: "250px";
    width: "150px";
    background: "white";
    max-height: "250px";
    overflow: "auto";
}

.history-menu-2 {
    height: 100%;
}

.line-div {
    border-radius: 5px;
    background: grey;
    border-color: black;
}

.remove-line {
    position: absolute;
    right: 0;
    z-index: false;
}
