.container{
    width: 100%; 
}

.expansion-container {
    width: 200px;
    box-shadow: inset 0px 0px 20px 8px rgb(235, 118, 8);
    border: 2px solid rgb(3, 0, 0);
}

.expansion-panel {
    width: 200px;
    box-shadow: inset 0px 0px 20px 8px rgb(235, 118, 8);
    border: 2px solid rgb(3, 0, 0);
}

.option-label {
    font-size: 15px;
    font-family: Impact, Charcoal, sans-serif;
    text-align: center;
    color: black;
    text-shadow: -1px 1 black, 1 3px black, 3px 1 black, 1 1px black;
}

.checkLine-container {
    display: flex;
    width: 200px;
    height: 30px;
}

.check-container {
    display: flex;
    justify-content: center;
}

.checkbox {
    color: rgb(139, 57, 2);
    width: 1px;
}

.search {
    width: 100%;
    display: flex;
}

.line-item {
    min-width: 150px;
    height: 30px;
    flex-direction: row-reverse;
}

.stop-item {
    width: 200px;
    display: flex;
    height: 50px;
    text-align: center;
    flex-direction: row-reverse grow;
}